import axios from 'axios'
export default {
    methods: {
        goBack() {
            this.$router.push('/');
        },
        signUp() {
            let validate = this.$refs.form.validate();
            if (!this.check) {

                this.alertDialog = true;
                this.alertType = 'warning';
                this.alertMessage = '  ';
            } else if (validate) {
                let member = {
                    id: this.id,
                    password: this.pwd1,
                    name: this.name,
                    zipcode: this.zipcode,
                    addr1: this.addr1,
                    addr2: this.addr2,
                    tel: this.tel,
                    email: this.email,
                    terms: this.agreement3,
                }
                axios.post('/api/member/insert', member)
                    .then(() => {
                        this.alertDialog = true;
                        this.alertType = 'success';
                        this.alertMessage = ' ';
                        this.alertPath = `/`;
                    }).catch(() => {
                        this.alertDialog = true;
                        this.alertType = 'warning';
                        this.alertMessage = ' ';
                    })
            }
        },
        signUpBySocial() {
            let validate = this.$refs.form.validate();
            if (validate) {
                let tempId = null;
                if (this.naverInfo != undefined) {
                    tempId = 'naver' + this.naverInfo.id;
                } else if (this.kakaoInfo != undefined) {
                    tempId = 'kakao' + this.kakaoInfo.id;
                }
                let member = {
                    id: tempId,
                    password: tempId + 'rh7369#n',
                    name: this.name,
                    zipcode: this.zipcode,
                    addr1: this.addr1,
                    addr2: this.addr2,
                    tel: this.tel,
                    email: this.email,
                    terms: this.agreement3,
                }
                axios.post('/api/member/insert', member)
                    .then(() => {
                        this.alertDialog = true;
                        this.alertType = 'warning';
                        this.alertMessage = ' ';
                        this.alertPath = `/`;
                    }).catch(() => {
                        this.alertDialog = true;
                        this.alertType = 'warning';
                        this.alertMessage = ' ';
                    })
            }
        },
        idCheck() {
            if (this.id.slice(0, 5) == 'kakao' || this.id.slice(0, 5) == 'naver') {
                this.alertDialog = true;
                this.alertType = 'warning';
                this.alertMessage = ' ';
                this.check = false;
            } else {
                axios.get(`/api/member/check/${this.id}`)
                    .then(() => {
                        this.alertDialog = true;
                        this.alertType = 'success';
                        this.alertMessage = ' ';
                        this.check = true;

                    })
                    .catch(() => {
                        this.alertDialog = true;
                        this.alertType = 'warning';
                        this.alertMessage = ' ';
                        this.check = false;

                    })
            }
        },
        execDaumPostcode() {
            new window.daum.Postcode({
                oncomplete: (data) => {
                    if (this.extraAddress !== "") {
                        this.addr2 = "";
                    }
                    if (data.userSelectedType === "R") {
                        //     
                        this.addr1 = data.roadAddress;
                    } else {
                        //     (J)
                        this.addr1 = data.jibunAddress;
                    }
                    // //       .
                    // if (data.userSelectedType === "R") {
                    //     //    . ( )
                    //     //     "//" .
                    //     if (data.bname !== "" && /[||]$/g.test(data.bname)) {
                    //         // this.addr2 += data.bname;
                    //     }
                    //     //  ,   .
                    //     if (data.buildingName !== "" && data.apartment === "Y") {
                    //         this.addr2 +=
                    //             this.addr2 !== "" ?
                    //             `, ${data.buildingName}` :
                    //             data.buildingName;
                    //     }
                    //     //    ,     .
                    //     if (this.addr2 !== "") {
                    //         this.addr2 = `(${this.addr2})`;
                    //     }
                    // } else {
                    //     this.addr2 = "";
                    // }
                    //  .
                    this.zipcode = data.zonecode;
                },
            }).open();
        }
    },
    watch: {
        id: {
            handler() {
                this.check = false;
            }
        }
    },
    mounted() {
        this.$vuetify.goTo(0);
        this.naverInfo = this.$route.params.naver;
        this.kakaoInfo = this.$route.params.kakao;
        if (this.naverInfo != undefined) {
            //    
            this.name = this.naverInfo.name;
            this.email = this.naverInfo.email;
        } else if (this.kakaoInfo != undefined) {
            //                
            this.name = this.kakaoInfo.kakao_account.profile.nickname;
        }
    },
    data() {
        return {
            alertDialog: false,
            alertMessage: '',
            alertType: '',
            alertPath: null,
            id: '',
            pwd1: '',
            pwd2: '',
            name: '',
            zipcode: '',
            addr1: '',
            addr2: '',
            tel: '',
            email: '',
            agreement1: '',
            agreement2: '',
            agreement3: '',
            naverInfo: '',
            check: false,
            rules: {
                id: [v => !!v || '  .',
                    v => /^[a-zA-Z0-9]*$/.test(v) || ' +  .',
                    v => v.length >= 4 || ' 4.',
                    v => v.length <= 24 || ' 24.',
                ],
                pwd1: [v => !!v || '  .',
                    v => /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^*+=-]).{0,}$/.test(v) || '  , ,  .',
                    v => v.length >= 8 || ' 8.',
                    v => v.length <= 20 || ' 20.',
                ],
                pwd2: [v => !!v || '  .',
                    v => v === this.pwd1 || '  .'
                ],
                name: [v => !!v || '  .', ],
                zipcode: [v => !!v || '  .', ],
                addr1: [v => !!v || '  .', ],
                addr2: [v => !!v || '  .', ],
                tel: [v => !!v || '  .',
                    v => /^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-?[0-9]{3,4}-?[0-9]{4}$/.test(v) || '   .',
                ],
                email: [v => !!v || '  .',
                    v => /^[A-Za-z0-9_\\.\\-]+@[A-Za-z0-9\\-]+\.[A-Za-z0-9\\-]+/.test(v) || '   .'
                ],
                agreement1: [v => !!v],
                agreement2: [v => !!v],
            },
        }
    },
}
