import axios from 'axios'
// import router from '@/router/index'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    data() {
        return {
            alertDialog: false,
            alertMessage: '',
            alertType: '',
            dialog: false,
            dialog2: false,
            dialog3: false,
            checkId: null,
            checkTel: null,
            pwd1: '',
            pwd2: '',
            id: '',
            password: '',
            rules: {
                id: [v => !!v || '  .',
                    v => /^[a-zA-Z0-9]*$/.test(v) || ' +  .',
                ],
                pwd: [v => !!v || '  .', ],
                pwd1: [v => !!v || '  .',
                    v => /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^*+=-]).{0,}$/.test(v) || '  , ,  .',
                    v => v.length >= 8 || ' 8.',
                    v => v.length <= 20 || ' 20.',
                ],
                pwd2: [v => !!v || '  .',
                    v => v === this.pwd1 || '  .'
                ],
                tel: [v => !!v || '  .',
                    v => /^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-?[0-9]{3,4}-?[0-9]{4}$/.test(v) || '   .',
                ],
            }
        }
    },
    methods: {
        goBack() {
            this.$router.push('/')
        },
        signIn() {
            let validate = this.$refs.form.validate();
            if (validate) {
                axios.get('/api/member/login', {
                        params: {
                            id: this.id,
                            password: this.password,
                        }
                    })
                    .then(res => {
                        let temp = res.data;
                        let user = {
                            id: temp.id,
                            name: temp.name,
                            authority: temp.authority,
                        };
                        this.Login({
                            user
                        })
                    })
                    .then(() => {
                        // console.log(this.getPath)
                        this.$router.push(this.getPath)
                    })
                    .catch(err => {
                        if (err.response.data == 'ID NOT FOUND') {
                            this.alertDialog = true;
                            this.alertType = 'warning';
                            this.alertMessage = '  ';
                            this.$refs.pwd.reset();
                            this.$refs.id.focus();
                        } else if (err.response.data == 'PASSWORD NOT MATCHED') {
                            this.alertDialog = true;
                            this.alertType = 'warning';
                            this.alertMessage = '  ';
                            this.$refs.pwd.reset();
                            this.$refs.pwd.focus();
                        }
                    })
            }
        },
        signUp() {
            this.$router.push('/authentication/signUp');
        },
        findID() {
            axios({
                    method: 'get',
                    url: `/api/member/find`,
                    params: {
                        tel: this.checkTel,
                    }
                })
                .then(res => {
                    this.alertDialog = true;
                    this.alertType = 'success';
                    this.alertMessage = ' ';
                    this.checkId = res.data;
                })
        },
        findPW() {
            axios({
                    method: 'get',
                    url: `/api/member/find`,
                    params: {
                        tel: this.checkTel,
                        id: this.checkId,
                    }
                })
                .then(res => {
                    if (res.data == 'yes') {
                        this.alertDialog = true;
                        this.alertType = 'success';
                        this.alertMessage = ' .  ';
                        this.dialog3 = true;
                    } else {
                        this.alertDialog = true;
                        this.alertType = 'warning';
                        this.alertMessage = '  ';
                    }
                })
        },
        updatePassword() {
            let member;
            if (this.pwd1 == this.pwd2) {
                member = {
                    id: this.checkId,
                    password: this.pwd1,
                }
            }
            axios.put('/api/member/updateMember', member)
                .then(() => {
                    this.alertDialog = true;
                    this.alertType = 'success';
                    this.alertMessage = ' ';
                    this.reset();
                })
                .catch(() => {
                    this.alertDialog = true;
                    this.alertType = 'error';
                    this.alertMessage = ' ';
                })
        },
        reset() {
            this.dialog3 = false;
            this.dialog2 = false;
            this.dialog = false;
            this.checkTel = null;
            this.checkId = null;
        },
        loginWithKakao() {
            if (window.Kakao.isInitialized() == false) {
                window.Kakao.init('255b187f87731368f5e47c3310b3cf02');
            }
            window.Kakao.Auth.login({
                success: this.kakaoLoginCheck(),
                fail: function (error) {
                    console.log(error);
                },
            })
        },
        kakaoLoginCheck() {
            window.Kakao.API.request({
                url: '/v2/user/me',
                success: res => {
                    axios.get('/api/member/login', {
                            params: {
                                id: 'kakao' + res.id,
                                password: 'kakao' + res.id + 'rh7369#n',
                            }
                        })
                        .then(result => {
                            let temp = result.data;
                            let user = {
                                id: temp.id,
                                name: temp.name,
                                authority: temp.authority,
                            };
                            this.Login({
                                user
                            })
                        })
                        .then(() => {
                            this.$router.push(this.getPath)
                        })
                        .catch(err => {
                            if (err.response.data == 'ID NOT FOUND') {
                                this.$router.push({
                                    name: "Social",
                                    params: {
                                        kakao: res,
                                    }
                                });
                            }
                        })
                },
                fail: error => {
                    this.$router.push("/authentication/signIn");
                    console.log(error);
                }
            })
        },
        naverLogin(token) {
            axios.get(`/api/member/getNaverLogin/${token}`)
                .then(res => {
                    axios.get('/api/member/login', {
                            params: {
                                id: 'naver' + res.data.response.id,
                                password: 'naver' + res.data.response.id + 'rh7369#n',
                            }
                        })
                        .then(res => {
                            let temp = res.data;
                            let user = {
                                id: temp.id,
                                name: temp.name,
                                authority: temp.authority,
                            };
                            this.Login({
                                user
                            })
                        })
                        .then(() => {
                            // console.log(this.getPath)
                            this.$router.push(this.getPath)
                        })
                        .catch(err => {
                            if (err.response.data == 'ID NOT FOUND') {
                                this.$router.push({
                                    name: "Social",
                                    params: {
                                        naver: res.data.response
                                    }
                                });
                            }
                        })
                })
        },
        ...LoginStore.mapActions(['Login']),
        ...LoginStore.mapMutations(['setPath']),
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
        ...LoginStore.mapGetters(['getPath'])
    },
    mounted() {
        this.$vuetify.goTo(0);
        if (this.$route.params.nextPage != null)
            this.setPath(`${this.$route.params.nextPage}`)
        else
            this.setPath('/')

        const naver_id_login = new window.naver_id_login("INmTkpuK5mPhbhHfYG_Q", "http://118.67.128.121:8085/authentication/signIn/naver");
        let link = document.location.href;
        if (link.indexOf('access_token') != -1) {
            this.naverLogin(naver_id_login.getAccessToken());
        } else {
            const state = naver_id_login.getUniqState();
            naver_id_login.setButton("white", 2, 40); //  
            naver_id_login.setState(state);
            // naver_id_login.setPopup(); // popup   
            naver_id_login.init_naver_id_login();
        }
    }
}
