import axios from 'axios'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    methods: {
        getAllMembers() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options;

            axios
                .get('/api/member/getMembers', {
                    params: {
                        page: page,
                        perPage: itemsPerPage,
                        condition: this.condition,
                        param: this.value,
                        role: this.showManager,
                    }
                })
                .then(res => {
                    this.items = res.data.res;
                    this.totalContents = res.data.count;
                })
                .catch(err => {
                    console.log(err.response.status);
                })
                .finally(this.loading = false);
        },
        searchMember() {
            if (this.options.page != 1) {
                this.options.page = 1
            } else {
                this.getAllMembers();
            }
        },
        telFormatter(tel) {
            let first;
            let second;
            let third;
            if (tel.length == 11) {
                first = tel.substr(0, 3);
                second = tel.substr(3, 4);
                third = tel.substr(7, 4);
                return `${first}-${second}-${third}`;
            } else if (tel.substr(0, 2) == '02' && tel.length == 10) {
                first = tel.substr(0, 2);
                second = tel.substr(2, 4);
                third = tel.substr(6, 4);
                return `${first}-${second}-${third}`;
            } else if (tel.substr(0, 2) == '02' && tel.length == 9) {
                first = tel.substr(0, 2);
                second = tel.substr(2, 3);
                third = tel.substr(5, 4);
                return `${first}-${second}-${third}`;
            } else if (tel.length == 10) {
                first = tel.substr(0, 3);
                second = tel.substr(3, 3);
                third = tel.substr(6, 4);
                return `${first}-${second}-${third}`;
            } else {
                return tel;
            }
        },
        selectItem(item) {
            let temp = this.items[
                this
                .items
                .indexOf(item)
            ];
            this.editItem = {
                id: temp.id,
                name: temp.name,
                tel: temp.tel,
                email: temp.email,
                zipcode: temp.zipcode,
                addr1: temp.addr1,
                addr2: temp.addr2,
                point: temp.point,
                pointTemp: temp.point,
                authority: temp.authority
            }
        },
        updateMember() {
            axios.put('/api/member/updateMember', this.editItem)
                .then(() => {
                    this.getAllMembers();
                    this.dialog = false;
                })
        },
        deleteMember() {
            axios.delete(`/api/member/delete/${this.id}`)
                .then(() => {
                    this.alertDialog = true;
                    this.alertType = 'success';
                    this.alertMessage = ' ';
                    this.editItem = {};
                    this.dialog2 = false;
                    this.dialog = false;
                    this.getAllMembers();
                })
                .catch(() => {
                    this.alertDialog = true;
                    this.alertType = 'warning';
                    this.alertMessage = '    ';
                })
        },
        reset() {
            this.condition = 'id';
            this.value = null;
            this.options.page = 1;
            this.options.itemsPerPage = 10;
            this.getAllMembers();
        },
        addManager() {
            let validate = this.$refs.form.validate();
            if (!this.check) {

                this.alertDialog = true;
                this.alertType = 'warning';
                this.alertMessage = '  ';
            } else if (validate) {
                let member = {
                    id: this.manager.id,
                    password: this.manager.pwd1,
                    name: this.manager.name,
                    tel: this.manager.tel,
                    authority: 'ROLE_ADMIN',
                }
                axios.post('/api/member/insert', member)
                    .then(() => {
                        this.alertDialog = true;
                        this.alertType = 'success';
                        this.alertMessage = '  ';
                        this.managerReset();
                        this.getAllMembers();
                    }).catch(() => {
                        this.alertDialog = true;
                        this.alertType = 'error';
                        this.alertMessage = ' ';
                    })
            }
        },
        idCheck() {
            if (this.manager.id == null) {
                return;
            } else if (this.manager.id.slice(0, 5) == 'kakao' || this.manager.id.slice(0, 5) == 'naver') {
                this.alertDialog = true;
                this.alertType = 'warning';
                this.alertMessage = ' ';
                this.check = false;
            } else {
                axios.get(`/api/member/check/${this.manager.id}`)
                    .then(() => {
                        this.alertDialog = true;
                        this.alertType = 'success';
                        this.alertMessage = ' ';
                        this.check = true;

                    })
                    .catch(() => {
                        this.alertDialog = true;
                        this.alertType = 'warning';
                        this.alertMessage = ' ';
                        this.check = false;

                    })
            }
        },
        pointCheck() {
            if (!(this.editItem.point >= 0 && (this.editItem.point == Math.round(this.editItem.point)) && this.editItem.point != '')) {
                this.alertDialog = true;
                this.alertType = 'warning';
                this.alertMessage = '  ';
                this.editItem.point = this.editItem.pointTemp;
                return;
            }
        },
        managerReset() {
            this.manager = {
                id: '',
                pwd1: '',
                pwd2: '',
                name: '',
                tel: '',
            }
            this.dialog3 = false
        }

    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: { //            
        options: {
            handler() {
                this.getAllMembers();
            },
            deep: true,
        },
        search: {
            handler() {
                this.value = '';
            }
        },
        showManager: {
            handler() {
                this.searchMember();
            }
        }
    },
    data() {
        return {
            alertDialog: false,
            alertMessage: '',
            alertType: '',
            dialog: false,
            dialog2: false,
            dialog3: false,
            showManager: false,
            check: false,
            totalContents: 0,
            loading: false,
            editItem: {},
            condition: 'id',
            value: '',
            searchType: [{
                text: 'ID',
                value: 'id'
            }, {
                text: '',
                value: 'name'
            }, {
                text: '',
                value: 'tel'
            }, {
                text: '',
                value: 'email'
            }, {
                text: '',
                value: 'zipcode'
            }, {
                text: '1',
                value: 'addr1'
            }, {
                text: '2',
                value: 'addr2'
            }, {
                text: '',
                value: 'point'
            }],
            options: {},
            headers: [{
                text: 'ID',
                value: 'id',
                divider: true,
                align: 'center',
                width: '10%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '',
                value: 'name',
                divider: true,
                align: 'center',
                width: '7%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '',
                value: 'tel',
                divider: true,
                align: 'center',
                width: '10%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '',
                value: 'email',
                divider: true,
                align: 'center',
                width: '12%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '',
                value: 'zipcode',
                divider: true,
                align: 'center',
                width: '7%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '1',
                value: 'addr1',
                divider: true,
                align: 'center',
                width: '22%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '2',
                value: 'addr2',
                divider: true,
                align: 'center',
                width: '12%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '',
                value: 'terms',
                divider: true,
                align: 'center',
                width: '8%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '',
                value: 'point',
                divider: true,
                align: 'center',
                width: '6%',
                sortable: false,
                class: 'text-subtitle-1'
            }, {
                text: '',
                value: 'btn',
                align: 'center',
                width: '6%',
                sortable: false,
                class: 'text-subtitle-1'
            }],
            items: [],
            manager: {
                id: '',
                pwd1: '',
                pwd2: '',
                name: '',
                tel: '',
            },
            rules: {
                id: [v => !!v || '  .',
                    v => /^[a-zA-Z0-9]*$/.test(v) || ' +  .',
                    v => v.length >= 4 || ' 4.',
                    v => v.length <= 24 || ' 24.',
                ],
                pwd1: [v => !!v || '  .',
                    v => /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^*+=-]).{0,}$/.test(v) || '  , ,  .',
                    v => v.length >= 8 || ' 8.',
                    v => v.length <= 20 || ' 20.',
                ],
                pwd2: [v => !!v || '  .',
                    v => v === this.manager.pwd1 || '  .'
                ],
                name: [v => !!v || '  .', ],
                tel: [v => !!v || '  .',
                    v => /^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-?[0-9]{3,4}-?[0-9]{4}$/.test(v) || '   .',
                ],
            },
        }
    }
}
